.sukuna-container{
    position: absolute;
    align-items: center;
    justify-content: center;
    transition: all .2s ease;
}
.sukunaCC {
    position: absolute;
    z-index: 99;
    background: url(./Assets/sukuna-sprite.png);
    /* animation: walk-sukuna steps(1) 1s infinite; */
    scale: 1;
    transition: all .2s ease, transform 0s;
}
.cleave {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 99;
    background: url(./Assets/sukuna-sprite.png);
    scale: 1;
}
.dismantle {
    position: absolute;
    width: 60px;
    height: 79px;
    transform: translate(-50%, -50%);
    z-index: 99;
    background: url(./Assets/sukuna-sprite.png) -871px -1681px;
    scale: .7;
    transition: left .2s;

}
.rapid {
    position: absolute;
    transform: translate(-60%, -70%);
    z-index: 99;
    background: url(./Assets/sukuna-sprite.png);
    scale: .8;
    width: 64px;
    height: 99px;
    animation: rapid 1.5s steps(1) infinite;
}
.domain-slash{
    position: absolute;
    transform: translate(-60%, -70%);
    z-index: 99;
    background: url(./Assets/sukuna-sprite.png);
    scale: .6;
    width: 64px;
    height: 99px;
    animation: domain-slash .4s steps(1) infinite;
    transform-origin: left top !important;
}
@keyframes domain-slash {
    0% {
        background-position: -977px -1683px;
        width: 64px;
        height: 97px;
    }

    50% {
        background-position: -1071px -1689px;
        width: 99px;
        height: 101px;
    }
    100% {
        background-position: -977px -1683px;
        width: 64px;
        height: 97px;    
    }
}
